@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

*[class*=title],
*[class*=heading],
*[class*=text] {
  margin: 0;
}

*[class*=link] {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.block-center {
  width: calc(100% - 40px);
  max-width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .block-center {
    width: calc(100% - 60px);
    max-width: 708px;
  }
}
@media (min-width: 1220px) {
  .block-center {
    width: calc(100% - 200px);
    max-width: 1220px;
  }
}

.button {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
  text-align: center;
  border: none;
  font-size: 16px;
  line-height: 20px;
}
.button--more {
  background-color: #f2f2f2;
}

.page--column {
  height: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
  font-size: 14px;
  line-height: 18px;
}
.page__body--column {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
@media (min-width: 768px) {
  .page__body {
    padding-top: 24px;
  }
}
@media (min-width: 1220px) {
  .page__body {
    padding-top: 53px;
  }
}

.page__header {
  position: relative;
  padding: 10px 0 0 0;
}
@media (min-width: 768px) {
