.navigation__toggler {
  justify-self: end;
  width: 24px;
  height: 18px;
  background-color: transparent;
  border: none;
  background-image: url("../img/burger.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.navigation__toggler--opened {
  background-image: url("../img/close.svg");
}
@media (min-width: 768px) {
  .navigation__toggler {
    display: none;
  }
}

.navigation__menu {
  display: none;
}
.navigation__menu--nojs {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.navigation__menu--opened {
  position: absolute;
  right: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .navigation__menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    justify-self: end;
    min-width: 440px;
  }
}
@media (min-width: 1220px) {
