}

.menu__item {
  width: 100%;
  border-bottom: 1px solid #e6e6e6;
}
.menu__item:first-of-type {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .menu__item {
    width: initial;
    border: none;
  }
  .menu__item:first-of-type {
    border: none;
  }
}

.menu__link {
  display: block;
  padding: 22px 0;
  color: inherit;
  font: inherit;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .menu__link {
    position: relative;
    padding: 0;
    min-height: 36px;
  }
  .menu__link--current::after {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    background-color: #68b738;
    content: "";
  }
  .menu__link--current-main::after {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    background-color: #68b738;
    content: "";
  }
}
@media (min-width: 1220px) {
  .menu__link--current-main::after {
    background-color: #ffffff;
  }
}

