.control {
  position: relative;
  padding-left: 45px;
}
.control::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  content: "";
}
.control::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 14px;
  height: 14px;
  background-color: #68b738;
  border: 1px solid #cdcdcd;
  content: "";
}
.control--radio::before {
  border-radius: 50%;
}
.control--radio::after {
  border-radius: 50%;
}
.control--checkbox::after {
  width: 2px;
  height: 8px;
}

.control__input {
  display: none;
}

.control__marker {
  font-size: 16px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .control__marker {
    font-size: 20px;
    line-height: 30px;
  }
}

